.greenline {
  position: relative;
}
.greenline::after {
  content: "";
  position: absolute;
  height: 2px;
  width: calc(100vw);
  background: #68B738;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}
@media (max-width: 767px) {
  .greenline::after {
    left: 245px;
  }
}
.greenline--addition::after {
  top: 19%;
}
@media (min-width: 768px) {
  .greenline::before {
    content: "";
    position: absolute;
    height: 2px;
    width: calc(100vw);
    background: #68B738;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
  }
  .greenline--addition::before {
    top: 19%;
  }
}
@media (max-width: 767px) {
  .greenline__span {
    background: transparent;
  }
}
@media (min-width: 768px) {
  .greenline__span {
    padding-right: 20px;
    position: relative;
    z-index: 10;
    background: #ffffff;
    padding-left: 20px;
  }
}
